@media (min-width: 600px) and (max-width: 800px) { 

    #root{
        background-size: 70%;
    }

    .fact{
        width: 85vw;
    }

    .body{
        margin-top: -20px;
    }

 }
 @media (min-width: 0px) and (max-width: 600px) { 

    #root{
        background-size: 80%;
    }

    .logo{
        position: relative;
        top: 15px;
    }

    .fact{
        font-size:24px;
        width: 350px;
    }

    .my-btn{
        font-size:28px;
    }

    .body{
        justify-content: space-around;
    }
 }